import Highway from '@dogstudio/highway';

class home extends Highway.Renderer {
    onEnter() {
        //SHOW AND HIDE SECTIONS

        let abt_show = document.querySelector("#js-links-show"),
        abt_hide = document.querySelector("#js-links-hide"),
        detail_show = document.querySelectorAll("#js-detail-links > a"),
        detail = document.querySelectorAll("#js-details > div");


        function hide_all_details(a=0){
        for ( var i = 0; i < detail.length; i++ ) {
        detail[i].style.display = "";
        }

        if(a==1){
        document.querySelector("#js-about > div:nth-child(2)").style.display = "none";
        }
        }


        abt_show.addEventListener("click", function(){
        document.querySelector("#js-about").style.display="block";
        })

        abt_hide.addEventListener("click", function(){
        document.querySelector("#js-about > div:nth-child(2)").style.display = "block";
        hide_all_details();
        document.querySelector("#js-about").style.display="";
        })

        detail_show[0].addEventListener("click", function(){
        hide_all_details(1);
        detail[0].style.display="block";
        })

        detail_show[1].addEventListener("click", function(){
        hide_all_details(1);
        detail[1].style.display="block";
        })

        detail_show[2].addEventListener("click", function(){
        hide_all_details(1);
        detail[2].style.display="block";
        })

        detail_show[3].addEventListener("click", function(){
        hide_all_details(1);
        detail[3].style.display="block";
        })

        detail_show[4].addEventListener("click", function(){
        hide_all_details(1);
        detail[4].style.display="block";
        })



        let bag = document.getElementById("js-bag"),
        bag_show = document.getElementById("js-bag-show"),
        bag_hide = document.getElementById("js-bag-hide");


        bag_show.addEventListener("click", function(){
        bag.style.display="block";
        })

        bag_hide.addEventListener("click", function(){
        bag.style.display="";
        })



        //LOGIN HIDE AND SHOW

        let sign_show = document.querySelector("#js-sign-show"),
        acc_hide = document.querySelector("#js-account-hide"),
        log_show = document.querySelector("#js-log-show");

        sign_show.addEventListener("click", function(){
        document.querySelector(".c-signup").style.display = "block";
        document.querySelector(".c-login").style.display = "none";
        })

        log_show.addEventListener("click", function(){
        document.querySelector(".c-signup").style.display = "none";
        document.querySelector(".c-login").style.display = "block";
        })

        acc_hide.addEventListener("click", function(){
        document.querySelector("#js-account").style.display = "none";
        })
    }
}

export default home;